import React, {Component} from 'react'
import {Tabs, Tab} from 'material-ui/Tabs';
import RaisedButton from 'material-ui/RaisedButton';


export default class Home extends Component {
  render() {
    return (
      <div style={{display: 'flex', flex: 1, flexDirection: 'column', backgroundColor: "#F7F8FA"}}>
        <div style={{display: 'flex'}}>
          <div style={{display: 'flex', flex: 1, backgroundColor: "#FFFFFF"}}/>

          <Tabs
            tabItemContainerStyle={{backgroundColor: "#FFFFFF"}}
            inkBarStyle={{backgroundColor: 'rgb(70, 160, 153)'}}
            style={{width: "50%"}}
          >
            <Tab style={styles.tab} label="主页"/>
            <Tab style={styles.tab} label="阅享"/>
            <Tab style={styles.tab} label="关于我们"/>
          </Tabs>

          <div style={{display: 'flex', flex: 1, backgroundColor: "#FFFFFF"}}/>
        </div>

        <div style={{display: 'flex', flex: 1, flexWrap: 'wrap', flexDirection: 'row', paddingTop: '3%', height: '100%'}}>
          <div style={{display: 'flex', flex: 0.8, }}/>
          <div style={{display: 'flex', flex: 1.3, flexDirection: 'column',  marginTop: '5%'}}>
            <p style={{fontSize: 42, color: 'rgb(83, 85, 88)'}}>阅享</p>
            <p style={{fontSize: 22, color: 'rgb(83, 85, 88)'}}>
              添加您拥有的和您想看的书籍，发现您与您好友之间共同感兴趣的阅读。充分利用闲置书籍。现支持iPhone设备。
            </p>
            <div style={{display: 'flex', justifyContent: 'flex-start'}}>
              <RaisedButton label="下载阅享" primary={true}/>
              <RaisedButton label="更多详情" primary={true} style={{marginLeft: 30}}/>
            </div>
          </div>
          <div style={{display: 'flex', flex: 1, flexDirection: 'column', marginLeft: 20,}}>
            <img src={require("./resources/ScreenShot_00.png")} style={{width: 520, height: 650}}/>
          </div>
          <div style={{display: 'flex', flex: 0.8, }}/>
        </div>
      </div>
    )
  }
}


const styles = {
  tab: {
    color: "#727272",
    fontWeight: 400
  },
};


